<template>
  <div class="wrapper">
    <div class="content">
      <h1>Thank you for your order!</h1>
      <center>
        <h2>Your order number is # {{ latestOrderID }}</h2>
      <br /><br />
        <p1 class="wantMore">Want more?</p1>
        &nbsp;
        <v-btn color="#0052A5" x-large @click="$router.push('/client')">
          <div class="completeButton">Order</div>
        </v-btn>
      </center>
    </div>
  </div>
</template>

<script>
export default {
  name: "LandingView",

  data: () => ({
    latestOrderID: 0,
    yourOrderNumber: 0,
    colorblind: false,
  }),

  components: {
    //HelloWorld,
  },

  methods: {
    toggleColorblind() {
        let main = document.getElementsByTagName("main").item(0);
        if (this.colorblind) {
            main.style.backgroundImage = `url()`;
        } else {
            main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117)), url(${require('./background.jpg')})`;
        }
        localStorage.setItem("colorblind",JSON.stringify(this.colorblind));
    },
},

  /**
   * it calls the functions that belong.
   * @async
   * @created
   */
  async created() {
    this.latestOrderID = parseInt(this.$route.query.latestOrderID);
    this.yourOrderNumber = this.latestOrderID + 1;
  },

  /**
   * it calls the functions that belong.
   * @async
   * @mounted
   */
  async mounted() {
    /*await promptLogin();*/
      if (JSON.parse(localStorage.getItem("colorblind"))) {
          this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
      }
      this.toggleColorblind();
  },
};
</script>

