.wrapper {
  display: grid;
  place-items: center;
  min-height: 100vh;
}

.content {
  font-family: system-ui, serif;
  font-size: 1.5rem;
  padding: 3rem;
  border-radius: 1rem;
}

.wantMore {
  font-family: system-ui, serif;
  font-weight: bold;
  font-size: 28px;
}

.completeButton {
  font-family: system-ui, serif;
  font-weight: bold;
  font-size: 14px;
}
</style>
