          console.log(await userSignedIn(e.credential));
          await translate('test','es', e.credential);
      }
  },

  /**
   * Initialize on startup
   * @async
   * @method
   */
  async mounted() {
      if (JSON.parse(localStorage.getItem("colorblind"))) {
          this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
      }
