<script>
import { translateAll, langs } from "../js/backend.js";
import { loadGoogle, userSignedIn } from "../js/login.js";
import { getItems, getLatestOrderId } from "../js/backend.js";
import { getLatestToppingUUID } from "../js/backend.js";
import { getLatestItemUUID } from "../js/backend.js";
import { insertOrder } from "../js/backend.js";
import { insertOrderItems } from "../js/backend.js";
import { insertOrderToppings } from "../js/backend.js";
import { getIdFromName } from "../js/backend.js";
import { incrementInventory } from "../js/backend.js";
import { getItemsFromCategory } from "../js/backend.js";
import { getToppings } from "../js/backend.js";

export default {
  // for checkout
  data: () => ({
    colorblind: false,
    checkoutDisabled: false,
    entree_name: "",
    entree_count: 0,
    isTrue: false,
    langs: [],
    drawer: false,
    signedIn: false,
    cart_total: 0,
    total: 0,
    sub_total: 0,
    sub_total2: 0,
    dialogDelete: false,
    count: -1,
    enable_toppings: false,
    enable_entrees: true,
    enalbe_mainProteins: false,
    enable_subProteins: false,
    enable_delete: false,
    tip: 0,
    calories_total: 0,
    calories_total2: 0,
    tip: false,
    tip: false,
    tip3: false,
    tip4: false,
    flag_delete: false,
    topping_count: 0,
    subProtein_count: 0,
    latestOrderID: 0,
    latestToppingUUID: 0,
    latestItemUUID: 0,

    justify: ["space-between"],

    headers: [
      {
        text: "Item Number",
        align: "start",
        sortable: false,
        value: "number",
      },
      { text: "Name", value: "name" },
      { text: "Price", value: "price" },
      { text: "Actions", value: "actions", sortable: false },
    ],

    items: [],
    order: [],
    order2: [],
    entrees_array: [],
    entrees_array2: [],
    toppings_array: [],
    toppings_array2: [],
    editedIndex: -1,
    editedItem: {
      number: 0,
      name: "",
      price: 0.0,
    },
    all_items: [],
    mainEntrees_all: [],
    mainEntrees_name: [],
    subEntrees_name: [],
    subEntrees_all: [],
    mainProteins_name: [],
    mainProteins_all: [],
    mainProteins_all2: [],
    subProteins_name: [],
    subProteins_all: [],
    toppings_name: [],
    toppings_all: [],
  }), // data end

  // computed
  computed: {
    /**
     * Stores data when reloading the page
     * @computed
     * @returns {entrees_array} entrees_array object
     */
    storeEntree() {
      if (this.entrees_array2.length != 0) {
        for (let i = 0; i < this.entrees_array2.length; i++) {
          this.entrees_array.push(this.entrees_array2[i]);
        }
      }
      if (this.toppings_array2.length != 0) {
        for (let i = 0; i < this.toppings_array2.length; i++) {
          this.toppings_array.push(this.toppings_array2[i]);
        }
      }
      if (this.order2.length != 0) {
        for (let i = 0; i < this.order2.length; i++) {
          this.order.push(this.order2[i]);
        }
      }
      this.calories_total = this.calories_total2;
      this.checkoutDisabled = true;
      this.enable_entrees = false;
      this.enable_toppings = false;
      this.enalbe_mainProteins = false;
      this.enable_subProteins = false;
      return this.entrees_array;
    },

    /**
     * it calculates total amount
     * @computed
     * @returns {sub_total} total amount
     */
    calculate_subTotal() {
      let sum = 0;
      for (let i = 0; i < this.entrees_array.length; i++) {
        sum += this.entrees_array[i].price;
      }
      for (let j = 0; j < this.toppings_array.length; j++) {
        sum += this.toppings_array[j].price;
      }
      return (this.sub_total = sum);
    },

    /**
     * it returns a boolean variable of enable_toppings
     * @computed
     * @returns {enable_toppings} boolean variable
     */
    isDisabled() {
      return this.enable_toppings;
    },

    /**
     * it returns a boolean variable of isDisabled_mainProteins
     * @computed
     * @returns {enalbe_mainProteins} boolean variable
     */
    isDisabled_mainProteins() {
      return this.enalbe_mainProteins;
    },

    /**
     * it returns a boolean variable of isDisabled_subProteins
     * @computed
     * @returns {enable_subProteins} boolean variable
     */
    isDisabled_subProteins() {
      return this.enable_subProteins;
    },

    /**
     * it returns a boolean variable of isDisabled_entrees
     * @computed
     * @returns {enable_entrees} boolean variable
     */
    isDisabled_entrees() {
      return this.enable_entrees;
    },

    /**
     * it calculates total calories
     * @computed
     * @returns {calories_total} total calories of order
     */
    calculate_total_calories() {
      return this.calories_total;
    },
  },

  // watch
  watch: {},

  // methods
  methods: {
    toggleColorblind() {
        let main = document.getElementsByTagName("main").item(0);
        if (this.colorblind) {
            main.style.backgroundImage = `url()`;
        } else {
            main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117)), url(${require('./background.jpg')})`;
        }
        localStorage.setItem("colorblind",JSON.stringify(this.colorblind));
    },
    /**
     * it reloads the page and go to the client page
     * @method
     */
    reloadPage() {
      this.$router.push({
        path: "client",
      });
      window.location.reload();
    },

    /**
     * it deletes entrees and toppings according to the item number
     * @method
     * @param {String} name - Item name
     * @param {Number} number - Item number
     */
    deleteEntree(name, number) {
      for (let i = 0; i < this.entrees_array.length; i++) {
        if (this.entrees_array[i].number === number) {
          this.sub_total -= this.entrees_array[i].price;
          this.entrees_array.splice(i, 1);
        }
      }
      var q = 0;
      while (q < this.toppings_array.length) {
        if (this.toppings_array[q].number === number) {
          this.sub_total -= this.toppings_array[q].price;
          this.toppings_array.pop(q);
        } else {
          ++q;
        }
      }
      var r = 0;
      while (r < this.order.length) {
        if (this.order[r].number === number) {
          this.remove_calories(this.order[r].name);
          this.order.splice(r, 1);
        } else {
          ++r;
        }
      }
      if (this.entrees_array.length == 0) {
        this.checkoutDisabled = false;
        this.enalbe_mainProteins = false;
        this.enable_subProteins = false;
        this.enable_toppings = false;
      }
      this.topping_count = 0;
    },

    /**
     * it increases the font size
     * @method
     */
    fontIncrease() {
      const allElements = document.getElementsByTagName("*");
      for (const element of allElements) {
        let style = window.getComputedStyle(element, null).getPropertyValue("font-size");
        let currentSize = parseFloat(style);
        element.style.fontSize = currentSize + 2 + "px";
      }
    },

    /**
     * it decreases the font size
     * @method
     */
    fontDecrease() {
      const allElements = document.getElementsByTagName("*");
      for (const element of allElements) {
        let style = window.getComputedStyle(element, null).getPropertyValue("font-size");
        let currentSize = parseFloat(style);
        element.style.fontSize = currentSize - 2 + "px";
      }
    },

    /**
     * it is Google translate API
     * @method
     * @async
     */
    async translateHandle(e) {
      await translateAll(e);
    },

    /**
     * it goes to the checkout page with some queries
     * @method
     */
    move() {
      this.$router.push({
        path: "checkout",
        query: {
          sub_total: this.sub_total,
          toppings_array: this.toppings_array,
          entrees_array: this.entrees_array,
          calories_total: this.calories_total,
          latestItemUUID: this.latestItemUUID,
          latestToppingUUID: this.latestToppingUUID,
          order: this.order,
        },
      });
    },

    /**
     * If the button name is one of the main entrees, it will push array object of
     * mainEntreess_all to items and entrees_array.
     * @method
     * @param {String} e - Item name
     */
    click_entrees(e) {
      for (let i = 0; i < this.mainEntrees_all.length; i++) {
        if (e === this.mainEntrees_all[i].name) {
          this.count += 1;
          this.calories_total += this.mainEntrees_all[i].calories;
          this.items.push({
            number: this.count,
            name: this.mainEntrees_all[i].name,
            price: this.mainEntrees_all[i].price,
          });
          this.entrees_array.push({
            number: this.count,
            name: this.mainEntrees_all[i].name,
            price: this.mainEntrees_all[i].price,
          });
        }
      }
      this.order.push({
        number: this.count,
        name: e,
      });
      this.flag_delete = false;
      this.$refs.step2[0].scrollIntoView({ behavior: "smooth" });
      this.checkoutDisabled = true;
    },

    /**
     * If the button name is one of the proteins, it will push array object of
     * mainProteins_all to items and toppings_array.
     * @method
     * @param {String} e - Item name
     */
    click_proteins(e) {
      if (this.flag_delete) {
        for (let i = 0; i < this.mainProteins_all.length; i++) {
          if (e === this.mainProteins_all[i].name) {
            this.calories_total += this.mainProteins_all[i].calories;
            this.items.splice(this.editedIndex, 0, {
              number: this.items[this.editedIndex - 1].number,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
          }
        }
      } else {
        for (let i = 0; i < this.mainProteins_all.length; i++) {
          if (e === this.mainProteins_all[i].name) {
            this.calories_total += this.mainProteins_all[i].calories;
            this.items.push({
              number: this.count,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
            this.toppings_array.push({
              number: this.count,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
          }
        }
        this.order.push({
          number: this.count,
          name: e,
        });
      }
      this.flag_delete = false;
      this.$refs.stepMid[0].scrollIntoView({ behavior: "smooth" });
    },

    /**
     * If the button name is one of the sub proteins, it will push array object of
     * mainProteins_all to items and toppings_array.
     * @method
     * @param {String} e - Item name
     */
    click_subProteins(e) {
      if (this.flag_delete) {
        for (let i = 0; i < this.mainProteins_all.length; i++) {
          if (e === this.mainProteins_all[i].name) {
            this.calories_total += this.mainProteins_all[i].calories;
            this.items.splice(this.editedIndex, 0, {
              number: this.items[this.editedIndex - 1].number,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
          }
        }
      } else {
        for (let i = 0; i < this.mainProteins_all.length; i++) {
          if (e === this.mainProteins_all[i].name) {
            this.calories_total += this.mainProteins_all[i].calories;
            this.items.push({
              number: this.count,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
            this.toppings_array.push({
              number: this.count,
              name: this.mainProteins_all[i].name,
              price: this.mainProteins_all[i].price,
            });
          }
        }
        this.order.push({
          number: this.count,
          name: e,
        });
      }
      this.flag_delete = false;
      this.subProtein_count += 1;
      this.$refs.step3[0].scrollIntoView({ behavior: "smooth" });
    },

    /**
     * If the button name is one of the toppings, it will push array object of
     * toppings_all to items and toppings_array.
     * @method
     * @param {String} e - Item name
     */
    click_toppings(e) {
      if (this.flag_delete) {
        for (let i = 0; i < this.toppings_all.length; i++) {
          if (e === this.toppings_all[i].name) {
            this.calories_total += this.toppings_all[i].calories;
            this.items.splice(this.editedIndex, 0, {
              number: this.items[this.editedIndex - 1].number,
              name: this.toppings_all[i].name,
              price: this.toppings_all[i].price,
            });
          }
        }
        let count = 4;
        count -= 1;
        this.enable_toppings_button(count);
      } else {
        for (let i = 0; i < this.toppings_all.length; i++) {
          if (e === this.toppings_all[i].name) {
            this.calories_total += this.toppings_all[i].calories;
            this.items.push({
              number: this.count,
              name: this.toppings_all[i].name,
              price: this.toppings_all[i].price,
            });
            this.toppings_array.push({
              number: this.count,
              name: this.toppings_all[i].name,
              price: this.toppings_all[i].price,
            });
          }
        }
        this.order.push({
          number: this.count,
          name: e,
        });
      }
      this.flag_delete = false;
      this.topping_count += 1;
      this.enable_toppings_button(this.topping_count);
    },

    /**
     * it enables or disables topping buttons
     * @method
     * @param {Number} e - number
     */
    enable_toppings_button(e) {
      if (this.topping_count > 3) {
        this.enable_toppings = false;
        this.$refs.step4[0].scrollIntoView({ behavior: "smooth" });
      } else {
        this.enable_toppings = true;
      }
    },

    /**
     * it enables or disables all buttons
     * @param {Number} k - number
     * @async
     */
    async enable_all_buttons(k) {
      let array = await getItemsFromCategory("mainEntree");
      for (let i = 0; i < array.length; i++) {
        if (k === array[i].name) {
          this.enable_toppings = true;
          this.enalbe_mainProteins = true;
          this.enable_subProteins = false;
        }
      }
    },

    /**
     * it enables entree buttons
     * @method
     * @param {Number} k - number
     */
    enable_entree_buttons(k) {
      this.enable_entrees = false;

      if (k === 1) {
        this.enable_entrees = true;
      }
    },

    /**
     * it disables main protein buttons
     * @method
     * @param {Number} k - number
     */
    disable_mainProteins_buttons(k) {
      this.enalbe_mainProteins = false;
    },

    /**
     * it disables main protein buttons
     * @method
     */
    enable_subProteins_buttons() {
      this.enable_subProteins = true;
    },

    /**
     * it disables sub protein buttons
     * @method
     */
    disable_subProteins_buttons() {
      this.enable_subProteins = false;
    },

    /**
     * it makes user add more items to the cart
     * @method
     */
    addItem() {
      this.enable_toppings = false;
      this.enable_entrees = true;
      this.enalbe_mainProteins = false;
      this.enable_subProteins = false;
      this.flag_delete = false;
      this.topping_count = 0;

      if (this.entrees_array.length != 0) {
        var a = 0;
        for (let i = 0; i < this.entrees_array.length; i++) {
          if (a < this.entrees_array[i].number) {
            a = this.entrees_array[i].number;
          }
        }
        this.count = a;
      }
      this.$refs.step1[0].scrollIntoView({ behavior: "smooth" });
    },

    /**
     * it automamtically calculates calories when the index of the array is removed
     * @method
     * @param {String} e - item name
     */
    remove_calories(e) {
      for (let i = 0; i < this.all_items.length; i++) {
        if (e === this.all_items[i].name) {
          this.calories_total -= this.all_items[i].calories;
        }
      }
    },

    /**
     * it gets all data and information from the database
     * @async
     * @method
     */
    async make_all_items() {
      this.all_items = await getItems();
      let array2 = await getItemsFromCategory("mainProtein");
      for (let p = 0; p < this.subProteins_all.length; p++) {
        array2.push(this.subProteins_all[p]);
      }
      for (let o = 0; o < this.toppings_all.length; o++) {
        array2.push(this.toppings_all[o]);
      }
      for (let u = 0; u < array2.length; u++) {
        this.all_items.push(array2[u]);
      }
    },

    /**
     * it clear all data
     * @method
     * @param {String} e - keyword
     */
    deleteAllItem(e) {
      if (e == "clear") {
        for (let i = this.items.length; i >= 0; i--) {
          this.items.pop(i);
        }
        this.entrees_array = [];
        this.entrees_array2 = [];
        this.toppings_array = [];
        this.toppings_array2 = [];
        this.count = -1;
        this.enalbe_mainProteins = false;
        this.enable_subProteins = false;
        this.enable_toppings = false;
        this.enable_entrees = true;
        this.tip = 0;
        this.total = 0;
        this.sub_total = 0;
        this.calories_total = 0;
        this.tip = false;
        this.tip = false;
        this.tip3 = false;
        this.tip4 = false;
        this.flag_delete = false;
        this.topping_count = 0;
        this.checkoutDisabled = false;
        this.order = [];
        alert("Cleared!");
      } else {
        for (let i = this.items.length; i >= 0; i--) {
          this.items.pop(i);
        }
      }
    },

    /**
     * it pops up the dialog message
     * @method
     * @param {String} data - keyword
     */
    getData(data) {
      alert(data);
    },

    /**
     * it gets the latest order ID
     * @async
     * @method
     */
    async getOrderID() {
      this.latestOrderID = await getLatestOrderId();
    },

    /**
     * it gets the latest topping UUID
     * @async
     * @method
     */
    async getToppingUUID() {
      this.latestToppingUUID = await getLatestToppingUUID();
    },

    /**
     * it gets the latest item UUID
     * @async
     * @method
     */
    async getItemUUID() {
      this.latestItemUUID = await getLatestItemUUID();
    },

    /**
     * it gets data from the database updates all buttons
     * @async
     * @method
     */
    async updateItems() {
      let a = await getItemsFromCategory("mainEntree");
      this.mainEntrees_name.splice(0);
      for (let i = 0; i < a.length; i++) {
        if (a[i].name != null) {
          this.mainEntrees_name.push(a[i].name);
        }
      }

      let b = await getItemsFromCategory("subEntree");
      this.subEntrees_name.splice(0);
      for (let j = 0; j < b.length; j++) {
        if (b[j].name != null) {
          this.subEntrees_name.push(b[j].name);
        }
      }

      let c = await getItemsFromCategory("mainProtein");
      this.mainProteins_name.splice(0);
      for (let n = 0; n < c.length; n++) {
        if (c[n].name != null) {
          this.mainProteins_name.push(c[n].name);
        }
      }

      let d = await getItemsFromCategory("subProtein");
      this.subProteins_name.splice(0);
      for (let m = 0; m < d.length; m++) {
        if (d[m].name != null) {
          this.subProteins_name.push(d[m].name);
        }
      }

      let e = await getItemsFromCategory("topping");
      this.toppings_name.splice(0);
      for (let u = 0; u < e.length; u++) {
        if (e[u].name != null) {
          this.toppings_name.push(e[u].name);
        }
      }
    },

    /**
     * it places an order
     * @async
     * @method
     */
    async insertOrderTable() {
      await insertOrder(
        this.latestOrderID + 1,
        this.calories_total,
        this.sub_total,
        this.tip,
        this.total,
        this.current_time
      );

      let c = 1;
      if (this.entrees_array != []) {
        for (let i = 0; i < this.entrees_array.length; i++) {
          let id_entrees = await getIdFromName(this.entrees_array[i].name);
          await insertOrderItems(
            this.latestItemUUID + c,
            this.latestOrderID + 1,
            this.entrees_array[i].number,
            id_entrees,
            this.entrees_array[i].name
          );
          let inventroy_id_entrees = await getIdFromName(this.entrees_array[i].name);
          await incrementInventory(inventroy_id_entrees);
          c += 1;
        }
      }

      let d = 1;
      if (this.toppings_array != []) {
        for (let j = 0; j < this.toppings_array.length; j++) {
          let id_toppings = await getIdFromName(this.toppings_array[j].name);
          await insertOrderToppings(
            this.latestToppingUUID + d,
            this.latestOrderID + 1,
            this.toppings_array[j].number,
            id_toppings,
            this.toppings_array[j].name
          );
          let inventory_id_toppings = await getIdFromName(this.toppings_array[j].name);
          await incrementInventory(inventory_id_toppings);
          d += 1;
        }
      }
      this.entrees_array = [];
      this.toppings_array = [];
      this.count = -1;
      this.latestOrderID = await getLatestOrderId();
      this.latestItemUUID = await getLatestItemUUID();
      this.latestToppingUUID = await getLatestToppingUUID();
      this.enalbe_mainProteins = false;
      this.enable_subProteins = false;
      this.enable_toppings = false;
      this.enable_entrees = true;
      this.tip = 0;
      this.total = 0;
      this.sub_total = 0;
      this.calories_total = 0;
      this.tip1 = false;
      this.tip2 = false;
      this.tip3 = false;
      this.tip4 = false;
      this.flag_delete = false;
      this.topping_count = 0;
    },

    /**
     * it gets data from the database
     * @async
     * @method
     */
    async getItemInfo() {
      this.mainEntrees_all = await getItems();
      this.mainProteins_all = await getItemsFromCategory("mainProtein");
      this.subProteins_all = await getItemsFromCategory("subProtein");
      for (let i = 0; i < this.subProteins_all.length; i++) {
        this.mainProteins_all.push(this.subProteins_all[i]);
      }
      this.mainProteins_all2 = await getItemsFromCategory("mainProtein");
      this.toppings_all = await getItemsFromCategory("topping");
    },
  },

  /**
   * it calls the functions that belong.
   * @async
   * @created
   */
  async created() {
    this.langs = langs;
    await this.updateItems();
    await this.getItemInfo();
    await this.make_all_items();
    await this.getOrderID();
    await this.getToppingUUID();
    await this.getItemUUID();
    this.drawer = this.$vuetify.breakpoint.mdAndUp;
    this.entrees_array2 = this.$route.query.entrees_array2;
    this.toppings_array2 = this.$route.query.toppings_array2;
    this.calories_total2 = parseInt(this.$route.query.calories_total2);
    this.sub_total2 = this.$route.query.sub_total2;
    this.order2 = this.$route.query.order2;
    this.storeEntree();
  },

  /**
   * it calls the functions that belong.
   * @async
   * @mounted
   */
  async mounted() {
      if (JSON.parse(localStorage.getItem("colorblind"))) {
          this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
      }
      this.toggleColorblind();
    //await this.getIdFromName();
  },
};
</script>
